import { React, useState, useEffect } from 'react'
import {
    Tooltip,
    Line,
    LineChart,
    XAxis,
    YAxis,
    Legend,
    ResponsiveContainer,
    Label,
} from 'recharts';
import { getStatSummary } from '../../utils/api';

export default function Stats() {
    const [fetching, setFetching] = useState(true);
    const [statsSummary, setStatsSummary] = useState({});
    const [walletsData, setWalletsData] = useState([]);
    const [transfersData, setTransfersData] = useState([]);

    const fetchData = async () => {
        const summary = await getStatSummary();
        const data = summary.result.data;
        setStatsSummary(data);
        setWalletsData(data.wallets);
        setTransfersData(data.transfers);
        setFetching(false);
    }

    useEffect(() => {
        fetchData();
    }, [])

    return (
        <div>
            <main className="stats">
                <div className="item">
                    <h3>Unique Wallets Created</h3>
                    <h1>
                        {!fetching ?
                            <span>{statsSummary.totalWallets}</span> :
                            <span>-</span>
                        }
                    </h1>
                </div>
                <div className="item end-right">
                    <h3>Total SPL Transfers</h3>
                    <h1>
                        {!fetching ?
                            <span>{statsSummary.totalTransfers}</span> :
                            <span>-</span>
                        }
                    </h1>
                </div>
            </main>

            <div className="graph-title">
                Cumulative MetaSPL transfers
            </div>

            <ResponsiveContainer
                width="95%"
                height={300}
            >
                <LineChart data={transfersData}>
                    <XAxis dataKey="day" />
                    <YAxis dataKey="transfers" >
                        <Label
                            value="transfers"
                            position="insideLeft"
                            angle={-90}
                            style={{ textAnchor: 'middle' }}
                        />
                    </YAxis>
                    <Tooltip />
                    <Legend />
                    <Line
                        type="monotone"
                        dataKey="transfers"
                        activeDot={{ r: 8 }}
                    />
                </LineChart>
            </ResponsiveContainer>

            <div className="graph-title">
                Cumulative wallets created
            </div>

            <ResponsiveContainer
                width="95%"
                height={300}
            >
                <LineChart data={walletsData}>
                    <XAxis dataKey="day" />
                    <YAxis dataKey="wallets">
                        <Label
                            value="wallets"
                            position="insideLeft"
                            angle={-90}
                            style={{ textAnchor: 'middle' }}
                        />
                    </YAxis>
                    <Tooltip />
                    <Legend />
                    <Line
                        type="monotone"
                        dataKey="wallets"
                        activeDot={{ r: 8 }}
                    />
                </LineChart>
            </ResponsiveContainer>
        </div >
    )
}