import axios from "axios";
import { relayerUrl } from "./constants";

export const getStatSummary = () => {
    return new Promise((resolve, reject) => {
        axios.get
            (
                `${relayerUrl}/get-stats`,
            )
            .then((result) => {
                resolve(result.data)
            })
            .catch((error) => {
                reject(error);
            })
    });
};
